import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { toast } from 'react-toastify';
import { RechargeApi } from 'utils/Api';

const Recharge = () => {
  const { t } = useTranslation();
  const [selectType, setSelectType] = useState({});
  const [amount, setAmount] = useState('');

  const handleSelect = (e) => {
		setSelectType({
			text: e.target.text,
			value: e.target.title
		})
		
  };

  const handleClick = (e) => {
    e.preventDefault();
    if (selectType.text === undefined) {
      toast.error('请选择充值方式');
			return 
    }
    if (amount === '') {
      toast.error('请输入要充值的金额');
			return
    }
		RechargeApi({payType: selectType, amount}).then((res) => {
			window.location.href = res.data
		})
  };
  return (
    <div className="flex flex-col justify-between h-full">
      <div className="dropdown relative">
        <button
          className="
					dropdown-toggle
          w-screen h-24  px-7 py-3 bg-orange-500 text-white font-bold text-sm leading-snug rounded shadow-md transition duration-150 ease-in-out flex
					justify-between
        	items-center
        	whitespace-nowrap"
          type="button"
          id="dropdownMenuLargeButton"
          data-bs-toggle="dropdown"
          aria-expanded="false"
        >
					{selectType.text === undefined ? '请选择充值方式': selectType.text}

          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="caret-down"
            className="w-2 ml-2"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 320 512"
          >
            <path
              fill="currentColor"
              d="M31.3 192h257.3c17.8 0 26.7 21.5 14.1 34.1L174.1 354.8c-7.8 7.8-20.5 7.8-28.3 0L17.2 226.1C4.6 213.5 13.5 192 31.3 192z"
            ></path>
          </svg>
        </button>
        <ul
          className="
        	dropdown-menu
        	min-w-full
        	absolute
        	hidden
        	bg-orange-500
        	z-50
        	float-left
        	py-2
        	list-none
        	text-left
        	rounded-lg
        	shadow-lg
        	mt-1
        	m-0
        	bg-clip-padding
        	border-none"
          aria-labelledby="dropdownMenuLargeButton"
        >
          <li>
            <a
              title={'300'}
              onClick={handleSelect}
              className="
            	dropdown-item
            	text-sm
            	py-2
            	px-4
            	font-bold
							text-white
            	block
            	w-full
            	whitespace-nowrap
            	bg-transparent
            	hover:bg-orange-400"
            >
              扫码付款
            </a>
          </li>
        </ul>
      </div>
      <div className=" flex flex-col items-center m-4">
        <div className="flex h-24 justify-center items-center gap-2">
          <label className="font-bold text-orange-600">充值金额:</label>
          <input
            type="number"
            value={amount}
            className="h-12 flex-auto outline-none px-2 rounded-lg border-2"
            onChange={(e) => setAmount(e.target.value)}
          ></input>
        </div>
        <button
          className="rounded-lg text-white font-bold bg-orange-600 h-12 w-24"
          onClick={handleClick}
        >
          确认提交
        </button>
      </div>
    </div>
  );
};

export default Recharge;
